@font-face {
	font-family: 'Unageo-Black';
	src: url(/fonts/UnageoBlack-Regular.ttf); 
}

@font-face {
	font-family: 'Unageo-Extrabold';
	src: url(/fonts/UnageoExtrabold-Regular.ttf); 
}

@font-face {
	font-family: 'Unageo-Light';
	src: url(/fonts/UnageoLight-Regular.ttf); 
}

@font-face {
	font-family: 'Unageo';
	src: url(/fonts/Unageo.ttf); 
}

@font-face {
	font-family: 'Unageo-Italic';
	src: url(/fonts/Unageo-Italic.ttf); 
}

:root {
  font-family: Unageo, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 100;
  text-align: start;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  min-height: 100%;
}

main {
  display: flex;
  flex-direction: column;
  gap: 70px;
  font-size: 16px;
}

h1 {
  font-family: 'Unageo';
  font-size: 40px;
  margin: 0px;
}

.h1 {
  max-width: 100%;
}

h2 {
  font-family: 'Unageo';
  font-size: 40px;
  margin-top: 0;
  margin-bottom: 0px;
  line-height: normal;
}

h3 {
  font-family: 'Unageo';
  box-sizing: border-box;
  margin: 0;
  margin-bottom: 10px;
  font-size: 53px;
  font-weight: 800;
  line-height: normal;
}

p {
  font-family: 'Unageo';
  box-sizing: border-box;
  margin: 0;
  font-size: 20px;
  font-weight: 300;
  line-height: 24px;
  color: #fff;
  padding: 0px;
}


button {
  min-width: 250px;
  font-family: Unageo,sans-serif;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 12px;
  border: none;
  outline: none;
  padding: 0px 20px;
  height: 50px;
  z-index: 5;
}

.input-color {
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
  height: 50px;
  width: 50px;
  padding: 0px 4px;
  background-color: #00000000;
  /* border: 1px solid #484848; */
  border-radius: 12px;
  display: flex;
  gap: 10px;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.recover-icon {
  position: absolute;
  pointer-events: none;
}

.change-color-button {
  background-color: #00000000;
  border: 1px solid #484848;
  border-radius: 12px;
  color: #ffffff;
  /* min-width: 100px; */
}

.change-color {
  display: flex;
  gap: 10px;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.input-file-label {
	position: relative;
	display: inline-block;

}
.input-file {
  display: none;
}

.input-file-btn {
	position: relative;
	display: flex;
  align-items: center;
  justify-content: center;
  width: 250px;
	cursor: pointer;
	outline: none;
  font-family: Unageo,sans-serif;
	text-decoration: none;
	vertical-align: middle;
  align-items: center;
  border: 1px solid #696969;
	color: rgb(255, 255, 255);
	text-align: center;
	border-radius: 12px;
	background-color: #2e2e2e;
	line-height: 22px;
	height: 50px;
	padding: 0px 20px;
	box-sizing: border-box;
  /* box-shadow: 0 0 70px #ffffff70; */
	/* border: none; */
  /* outline: 1px solid #484848; */
	margin: 0;
	transition: background-color 0.2s;
  transition: all 0.2s;
}

.control-panel {
  max-width: 1100px;
  display: flex;
  gap: 10px;
  flex-direction: row;
  align-items: start;
  justify-content: space-between;
  width: 100%;
  padding: 30px 30px;
  border-bottom: 1px solid #212121;
  border-top: 1px solid #212121;
}

.delete-btn {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 10px;
}

.delete-btn p {
  font-size: 16px;
  color: #dbdbdb;
}

.delete-icon {
  width: 22px;
}

.change-color-container {
  display: flex;
  gap: 30px;
  flex-direction: column;
  align-items: center;
  justify-content: start;
}

body {
  /* font-family: 'Unageo';
  font-weight: 100; */
  margin: 0;
  display: flex;
  /* place-items: center; */
  min-width: 320px;
  min-height: 100vh;
  background-color: #111111;
  color: #fff;
}

.switch-container {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
  justify-content: start;
  min-width: 240px;
}

.switch-description {
  font-size: 16px;
  color: #a0a0a0;
}

.download-container {
  display: flex;
  gap: 30px;
  flex-direction: column;
  align-items: center;
  justify-content: start;
}

.download-code-svg {
  font-size: 16px;
  cursor: pointer;
}

.author-info {
  position: absolute;
  bottom: 0px;
  font-size: 14px;
  color: #555555;
  text-align: center;
  margin-top: 20px;
}

.author-link {
  color: #5f5f5f;
  text-decoration: underline;
  font-weight: 400;
  transition: all 0.2s ease;
}

.author-link:hover {
  transition: all 0.2s ease;
  color: #686868;
}


input {
  min-height: 100px;
  min-width: 100px;
  padding: 0px 4px;
  background-color: #00000000;
  /* border: 1px solid #ffffff; */
}


@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
}
